import MescrollItem from "./lower-swiper-item.vue";

export default {
	components: {
		MescrollItem
	},
	data() {
		return {
			tabs: [{
					title: '全部',
					status: 0
				},
				{
					title: '已付款',
					status: 12
				},
				{
					title: '已结算',
					status: 3
				},
				{
					title: '已关闭',
					status: 13
				}],
			curIndex: 0, // 当前tab的下标
			status: 0
		}
	},
	methods: {
		// 轮播菜单
		swiperChange(e){
			let status = this.tabs[e.detail.current].status
			this.changeTab(status,e.detail.current)
		},
		// 切换菜单
		changeTab(sta,i){
			this.curIndex = i;
			this.status = sta
		}
	}
}
